.loader{position:relative;min-height:100px}.loader__overlay{position:absolute;top:0;left:0;right:0;bottom:0}.loader__spinner{border-radius:50%;width:5em;height:5em;margin:60px auto;font-size:10px;position:relative;text-indent:-9999em;border-top:1.1em solid rgba(24,144,255,0.2);border-right:1.1em solid rgba(24,144,255,0.2);border-bottom:1.1em solid rgba(24,144,255,0.2);border-left:1.1em solid #1890ff;-webkit-transform:translateZ(0);transform:translateZ(0);-webkit-animation:load8 1.1s infinite linear;animation:load8 1.1s infinite linear;z-index:1}.loader__spinner:after{border-radius:50%;width:10em;height:10em}@-webkit-keyframes load8{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes load8{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}

.header{position:-webkit-sticky;position:sticky;top:0;z-index:1;background-color:white;box-shadow:0 0 5px 1px black;display:flex;justify-content:center}

.button-load-more{position:fixed !important;right:20px;bottom:40px}

.comments-title{margin-top:24px;margin-bottom:0}

.app{flex-grow:1}.app_wrapper{min-height:100vh;display:flex;flex-direction:column;background-color:lightblue}

.cursor-pointer{cursor:pointer}body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}#root{min-height:100vh}.container{padding:16px}

